@use 'scss-reset/reset';

// fonts 
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500&display=swap');
//

header {
   height: 90px;
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 0px 40px;
}


.logo>p:first-child {
   font-family: 'Roboto', sans-serif;
   font-size: 24px;
   color: rgb(20, 20, 20);
}

.logo {
   display: flex;
   align-items: center;
   cursor: pointer;

   div {
      margin-left: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: rgb(245, 180, 59);
      border-radius: 9px;
      -webkit-border-radius: 9px;
      -moz-border-radius: 9px;
      -ms-border-radius: 9px;
      -o-border-radius: 9px;
      width: 20px;
      height: 23px;
      line-height: 23px;

      p {
         color: white;
         font-weight: 600;
         font-size: 14px;
         text-align: center;
      }
   }
}

.header-buttons {
   display: flex;
}

.list-view {
   width: 80px;
   height: 40px;
   background-color: #6d81c4;
   border: solid 1px grey;

   p {
      color: white;
      font-size: 12px;
   }
}


.position-arr {
   display: flex;
   position: relative;
   margin: 0px 5px;
   width: 40px;
   height: 40px;
   background-color: #969696;
   border: solid 1px grey;

   img {
      width: 50%;
   }

   button {
      position: relative;
   }

   .tooltip-text {
      display: none;
      position: absolute;
      top: 115%;
      left: 5%;
      transform: translateX(-50%);
      background-color: #969696;
      color: #fff;
      padding: 1px 10px;
      border-radius: 5px;
      white-space: nowrap;
   }
}

.tooltip-text::before {
   content: "";
   position: absolute;
   bottom: 100%;
   left: 50%;
   margin-left: -5px;
   border-width: 5px;
   border-style: solid;
   border-color: transparent transparent #969696 transparent;
}


.position-arr:hover .tooltip-text {
   display: block;
}


.zoom {
   display: flex;

   .zoom-btn {
      width: 40px;
      height: 40px;
      border: solid 1px rgb(160, 159, 159);

      img {
         width: 30%;
         opacity: 0.7;
      }
   }

   .custom-select {
      text-align: center;
      position: relative;
      width: 100px;
      margin: 0px 5px;

      .select-selected {
         background-color: #f1f1f1;
         padding: 10px;
         border: 1px solid #ccc;
         cursor: pointer;

         &.active {
            border-bottom-left-radius: 0;
            border-bottom-right-radius: 0;
         }
      }

      .select-items {
         display: none;
         position: absolute;
         background-color: #f1f1f1;
         min-width: 100%;
         border: 1px solid #ccc;

         &.visible {
            display: block;
            z-index: 2;
         }
      }

      .select-item {
         padding: 10px;
         cursor: pointer;

         &.selected {
            background-color: #ddd;
         }
      }

      .checkmark {
         margin-left: 5px;
         color: #6d81c4;
         font-weight: 600;
      }
   }
}

main {
   display: flex;
   flex-direction: column;
   align-items: center;
   height: calc(100vh - 90px);

   .line {
      height: 0.3px;
      width: calc(100% - 70px);
      background-color: rgb(206, 206, 206);
   }

   section {
      display: flex;
      justify-content: space-between;
      height: 100%;
      width: 100%;
   }
}

.wraper-x {
   width: 100%;
   display: flex;
   justify-content: center;

   button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100px;
      height: 40px;
      background-color: rgb(190, 190, 190);
   }
}

.wraper-y {
   display: flex;
   align-items: center;

   button {
      display: flex;
      align-items: center;
      justify-content: center;
      width: 40px;
      height: 100px;
      background-color: rgb(190, 190, 190);
   }
}

.diagram-place {
   display: flex;
   z-index: 1;
   position: relative;
   overflow: hidden;

   #diagram {
      display: flex;
      flex-direction: column;
      align-items: center;
      top: 25%;
      left: calc(50% - 120px/2);
      position: fixed;
      cursor: pointer;

      .div-diagram {
         display: flex;
         align-items: center;

         .wraper-cat {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 120px;
            height: 45px;
            background-color: rgb(243, 243, 243);
            border: 1px solid rgb(180, 180, 180);

            p {
               font-size: 20px;
               font-weight: 600;
            }
         }
      }

      .sub-cat {
         display: flex;
      }
   }
}

.block {
   margin: 20px;
   display: flex;
   align-items: flex-start;

   div {
      display: flex;
      align-items: center;
      justify-content: center;

      input {
         width: 120px;
         padding-left: 5px;
      }
   }

   .one-sub-cat {
      display: flex;
      flex-direction: column;

      .block-cat {
         display: flex;
         justify-content: center;
         align-items: center;

         .cat-name {
            padding: 10px 20px;
            background-color: bisque;
         }
      }

      div {
         display: flex;
         justify-content: center;
      }
   }
}